/* General body styles */
body {
  background-color: #0e131b; /* Dark background similar to a blackjack table in a dim room */
  color: white;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Main container for the blackjack game */
.blackjack-container {
  text-align: center;
  background-color: #1c222d; /* Dark gray-blue container background */
  border-radius: 10px;
  padding: 20px;
  width: 900px; 
  height: auto; 
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Title styling */
.game-title {
  font-size: 3em;
  color: white; /* Light gray for the title text */
  margin-bottom: 20px;
}

/* Main game area */
.game-area {
  background: #161a23; /* Dark background for the game area */
  border-radius: 10px;
  padding: 30px;
  height: 100%; 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0px 0px 10px rgba(255, 255, 255, 0.1);
}

/* Hands container for player and dealer */
.hands-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; /* Allow wrapping for responsiveness */
  height: auto; 
}

/* Hand section (player and dealer) */
.hand {
  width: 45%; 
  padding: 20px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Card display area */
.cards {
  background-color: #2c2f3a; /* Dark card area background */
  border-radius: 8px;
  padding: 10px; /* Reduced padding to accommodate dynamic card size */
  width: 100%; 
  display: flex;
  flex-wrap: wrap; /* Allow cards to wrap if needed */
  justify-content: center;
  align-items: center;
  color: #8c9196; /* Light gray card text color */
  margin-bottom: 20px; 
}

.cards img {
  width: 100px;
  height: 140px;
  margin: 5px; /* Adjust margin for spacing between cards */
}

/* Button container for Deal, Hit, and Stay */
.button-container {
  display: flex;
  justify-content: space-between;
  align-items: center; 
  margin-bottom: 20px;
}

.left-buttons {
  display: flex;
  gap: 10px; 
}

/* Turquoise Styling */
.game-button {
  background-color: #17a2b8;
  color: white;
  border: none;
  padding: 15px;
  font-size: 1.2em;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  height: 60px; 
  width: 140px;
}

.new-game-button {
  width: 100%;
  height: 60px;
  background-color: #17a2b8;
  color: white;
  border: none;
  padding: 15px;
  font-size: 1.2em;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.new-game-button:hover,
.game-button:hover {
  background-color: #1699ad;
  transform: scale(1.05);
}

.deal-button {
  width: 200px; 
  padding: 20px; 
  font-size: 1.2em; 
  height: 60px; 
  background-color: #17a2b8;
  color: white;
}

.deal-button:hover {
  background-color: #1699ad;
}

/* Balance and Bet container */
.balance-bet-container {
  display: flex;
  justify-content: space-between; /* Space between balance and bet input */
  align-items: center; /* Center items vertically */
  margin-bottom: 20px; /* Adjust margin as needed */
}

/* Balance info styling */
.balance-info {
  padding: 15px; /* Padding for spacing */
  border-radius: 5px; /* Rounded corners */
  font-size: 1.4em; /* Font size for better readability */
}
.bet-input-container{
  font-size: 1.4em;
}

/* Bet input field styling */
.bet-input {
  background-color: #1c222d; 
  color: #ffffff; 
  border: 2px solid; 
  border-radius: 12px; 
  padding: 12px 20px; 
  font-size: 1.4em; 
  width: 220px;
  text-align: center;
  margin: 0;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
}

/* Input inside bet input field styling */
.bet-input input {
  background-color: #1c222d;
  color: white;
  border: none; /* Remove border for the inner input */
}

/* Result section */
.result-container {
  margin-top: 10px;
}

.result-container h2 {
  font-size: 1.8em;
  color: #8c9196; /* Gray color for the result section */
}

.result-container p {
  font-size: 1.5em;
  color: white;
  margin-top: 10px;
}

/* Responsive design */
@media (max-width: 768px) {
  .blackjack-container {
    width: 90%;
    height: auto;
  }

  .hands-container {
    flex-direction: column;
    height: auto;
  }

  .hand {
    width: 100%;
    margin-bottom: 20px;
  }

  .cards img {
    width: 70px;
    height: 100px;
  }

  .game-button {
    width: 120px; 
    height: 50px; 
  }

  .deal-button {
    width: 150px; 
    padding: 15px; 
    font-size: 1.2em;
    height: 50px;
  }

  .bet-input {
    width: 180px; /* Adjust width for smaller screens */
    font-size: 1.2em; /* Slightly smaller font size */
  }
}
