.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .popup-inner {
    position: relative;
    background-color: #1c222d;
    border-radius: 10px;
    padding: 32px;
    max-width: 640px;
    width: 90%;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.7);
    text-align: center;
    color: white;
    animation: slideIn 0.3s ease-in-out;
  }
  .close-button {
    position: absolute;
    top: 0px;
    right: 0px;
    background-color: #1c222d;
    color: gray;
    border: none;
    font-size: 1em;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
    margin-bottom: 15px;
  }
  
  .close-button:hover {
    transform: scale(1.05);
  }
  @keyframes slideIn {
    from {
      transform: translateY(-20%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  @media (max-width: 768px) {
    .popup-inner {
      max-width: 90%;
      padding: 20px;
    }
  
    .close-button {
      font-size: 0.9em;
      padding: 8px 16px;
    }
  }
  